<template>
    <div>
        <div style="overflow: hidden">
            <div style="float:left" class="user-info-label">
                <div class="name-label">
                    <span>你</span>
                </div>
                <img :src="this.$StaticImage + myinfo.profile" class="round_icon"  alt="我的头像">
                <div class="name-label">
                    <span>{{myinfo.nickname}}</span>
                </div>
                <div style="text-align: center">
                    <img :src="this.$StaticImage + this.turnImg" v-if="me == true">
                </div>
            </div>
            <div style="float: left;height: 100px;padding-top: 50px;margin-left: 100px;vertical-align: center">
                <div style="float:left">{{myready}}</div>
                <img :src="this.$StaticImage + this.vsImg" alt="对决" style="width: 150px; height: 120px;float: left">
                <div style="float: left">{{yourready}}</div>
            </div>
            <div style="float:right" class="user-info-label">
                <div class="name-label">
                    <span>对手</span>
                </div>
                <img :src="this.$StaticImage + yourinfo.profile" class="round_icon" alt="对方的头像">
                <div class="name-label">
                    <span>{{yourinfo.nickname}}</span>
                </div>
                <div style="text-align: center">
                    <img :src="this.$StaticImage + this.turnImg" v-if="me == false">
                </div>
            </div>
        </div>
        <div style="overflow: hidden; margin-top: 20px">
            <div style="float:left; margin-left: 300px;">
                <canvas ref="chess" width="600" height="600" @click="chessClick"></canvas>
            </div>
            <div style="float:right;margin-right: 100px">
                <ChatRoom :myinfo="myinfo" :yourinfo="yourinfo" :chatrecord="chatrecord" :roomid="roomid" @func="chatSendMessage"></ChatRoom>
            </div>
        </div>
        <div style="text-align: center">
            <el-button round :disabled="btnable" @click="ReadytoPlay">准备</el-button>
            <el-button round @click="BacktoHome">退出</el-button>
        </div>
    </div>
</template>

<script>
    import {Message} from 'element-ui'
    import ChatRoom from "@/components/ChatRoom";

    // 状态码
    //  1000:加入房间
    //  1001: 服务器告知并获取对方信息
    //  1002：准备
    //  1003：开始
    //  1004：下棋
    //  1005: 聊天
    //  1006：比赛结束
    //  1050: 退出该界面

    export default {
        name: "Bang",
        components: {ChatRoom},
        data(){
            return{
                chess: {},
                context: {},
                chessBoard: [], // 记录是否走过
                me: false,
                color:-1, //黑为1，白为2
                count: 0, // 所有赢法数量
                over: false,
                vsImg : '/images/chess-VS.jpg',
                turnImg : '/images/arrow-turn.svg',
                myinfo:{
                    profile:"/images/no-profile.jpg",
                    nickname:"暂无",
                    username:"",
                },
                roomid:'',
                yourinfo:{
                    profile:"/images/no-profile.jpg",
                    nickname:'暂无',
                    username:'',
                },
                chatrecord : [],
                param:{},
                step:{},
                beforeStep:{},
                myready:'未准备',
                yourready:'未准备',
                btnable:false,
                chesssize:15, //棋盘大小
                gridsize:40, //格子大小
                chesswidth:0
            }
        },

        mounted(){
            this.chesswidth = this.chesssize * this.gridsize
            this.init()
            this.getUserInfo()
        },

        methods:{
            init() {
                this.chess = this.$refs.chess
                this.initChessBoard()
                this.roomid = window.location.href.split('=')[1]
            },

            initChessBoard(){
                this.over = false
                this.context = this.chess.getContext('2d')
                this.context.height = this.chess.height
                this.drawImage(_ => {
                    this.drawChessBoard()
                })
                this.fillArray()
                this.beforeStep = {x:-1, y:-1}
            },

            fillArray(){
                for(let i = 0;i < this.chesssize;i ++){
                    this.chessBoard[i] = []
                    for(let j = 0;j < this.chesssize;j ++){
                        this.chessBoard[i][j] = 0
                    }
                }
            },

            drawImage(callback){
                const {context} = this
                context.fillStyle = 'rgb(213,176,146)'
                context.fillRect(0,0,this.chesswidth,this.chesswidth)
                context.stroke()
                const img = new Image()
                img.src = this.$StaticImage + "/images/chess-bg.svg"
                img.onload = _ =>{
                    context.drawImage(img, (this.chesswidth - img.width) / 2, (this.chesswidth - img.height) / 2, img.width, img.height)
                    callback()
                }
            },

            drawChessBoard(){
                const { context } = this
                context.strokeStyle = '#2b2b2b'
                for(let i = 0;i < this.chesssize;i ++){
                    context.moveTo(this.gridsize / 2  + i * this.gridsize, this.gridsize / 2)
                    context.lineTo(this.gridsize / 2  + i * this.gridsize, this.chesswidth - this.gridsize / 2)
                    context.stroke()
                    context.moveTo(this.gridsize / 2, this.gridsize / 2  + i * this.gridsize)
                    context.lineTo(this.chesswidth - this.gridsize / 2, this.gridsize / 2  + i * this.gridsize)
                    context.stroke()
                }
            },

            EmptyStep() {
                this.step ={
                    player:'',
                    coordinate:{},
                    color:-1,
                }
            },

            EmptyParam(){
                this.param = {}
            },

            SetStep(player, step, color){
                if(player != null && step != null && color != null){
                    this.step.player = player
                    this.step.coordinate = step
                    this.step.color = color
                }
            },

            chessClick(ev){
                if(this.over){
                    return
                }
                if(!this.me){
                    return
                }
                const ox = ev.offsetX
                const oy = ev.offsetY
                const x = Math.floor(ox / this.gridsize)
                const y = Math.floor(oy / this.gridsize)
                if(this.chessBoard[x][y] == 0){
                    let coordinate = {x:x , y:y}
                    this.SetStep(this.myinfo.username, coordinate, this.color)
                    this.step.rid = this.roomid
                    this.setParam(1004, this.step)
                    this.$ws.dispatch("WEBSOCKET_SEND", JSON.stringify(this.param))
                }
            },

            onStep(coordinate, color, radius){
                const x = coordinate.x
                const y = coordinate.y
                this.chessBoard[x][y] = color
                const {context} = this
                context.beginPath()
                context.arc(this.gridsize / 2 + x * this.gridsize, this.gridsize / 2 + y * this.gridsize, radius, 0 , 2 * Math.PI)
                context.closePath()
                // const gradient = context.createRadialGradient(15 + x * 30 + 2, 15 + y * 30 - 2, 13, 15 + x * 30 + 2, 15 + y * 30 - 2, 0)
                const gradient = context.createRadialGradient(this.gridsize / 2 + x * this.gridsize + 2, this.gridsize / 2 + y * this.gridsize - 2, 13,
                    this.gridsize / 2 + x * this.gridsize  + 2, this.gridsize / 2 + y * this.gridsize - 2, 0)
                if(color == 1){
                    gradient.addColorStop(0, '#0a0a0a')
                    gradient.addColorStop(1, '#636766')
                }
                else if(color == 2){
                    gradient.addColorStop(0, '#d1d1d1')
                    gradient.addColorStop(1, '#f9f9f9')
                }
                context.fillStyle = gradient
                context.fill()
            },

            circleChess(coordinate){
                const x = coordinate.x
                const y = coordinate.y
                const {context} = this
                context.strokeStyle = '#ff3c3e'
                context.arc(this.gridsize / 2 + x * this.gridsize, this.gridsize / 2 + y * this.gridsize, this.gridsize / 2 - 2 , 0, 2 * Math.PI)
                context.stroke()
            },

            changeBeforeStep(coordinate){
                if(this.beforeStep.x != -1 && this.beforeStep.y != -1) {
                    const {context} = this
                    const x = this.beforeStep.x
                    const y = this.beforeStep.y
                    context.clearRect(x * this.gridsize, y * this.gridsize, this.gridsize, this.gridsize)
                    context.fillStyle = 'rgb(213,176,146)'
                    context.fillRect(x * this.gridsize,y * this.gridsize,this.gridsize, this.gridsize)
                    this.onStep(this.beforeStep, this.chessBoard[this.beforeStep.x][this.beforeStep.y], this.gridsize / 2 - 3)
                }
                this.beforeStep = coordinate
            },

            checkOver(coordinate, color){
                //检查纵向向
                const x = coordinate.x
                const y = coordinate.y
                let sx = x < 4 ? 0 : x - 4
                let ex = x > 10 ? 14 : x + 4
                let count = 0
                for(let i = sx;i <= ex;i ++){
                    if(this.chessBoard[i][y] == color){
                        count ++
                    }
                    else{
                        count = 0
                    }
                    if(count == 5){
                        this.over = true
                        return true
                    }
                }

                //检查横向
                let sy = y < 4 ? 0 : y - 4
                let ey = y > 10 ? 14 : y + 4
                count = 0
                for(let i = sy;i <= ey;i ++){
                    if(this.chessBoard[x][i] == color){
                        count ++
                    }
                    else{
                        count = 0
                    }
                    if(count == 5){
                        this.over = true
                        return true
                    }
                }

                //左上到右下斜线
                sx = x - 4
                sy = y - 4
                ex = x + 4
                ey = y + 4
                count = 0
                for(let i = sx, j = sy;i <= ex && j <= ey;i ++, j ++){
                    if(i < 0 || j < 0 || i > 14 || j > 14)
                        continue
                    if(this.chessBoard[i][j] == color){
                        count ++
                    }
                    else{
                        count = 0
                    }
                    if(count == 5){
                        this.over = true
                        return true
                    }
                }

                //右上到左下斜线
                sx = x - 4
                sy = y + 4
                ex = x + 4
                ey = y - 4
                count = 0
                for(let i = sx, j = sy;i <= ex && j >= ey;i ++, j --){
                    if(i < 0 || j < 0 || i > 14 || j > 14)
                        continue
                    if(this.chessBoard[i][j] == color){
                        count ++
                    }
                    else{
                        count = 0
                    }
                    if(count == 5){
                        this.over = true
                        return true
                    }
                }
                return false
            },

            getUserInfo(){
                let username = window.sessionStorage.getItem("user")
                if(username == null){
                    window.sessionStorage.removeItem("user")
                    this.$router.push("/")
                    return ;
                }
                let url = "/user/?username=" + username
                this.getRequest(url).then(resp => {
                    this.myinfo = resp
                    let data = {
                        "id": this.myinfo.username,
                        "rid": this.roomid,
                    }
                    this.setParam(1000, data)
                    this.$ws.dispatch('WEBSOCKET_SEND', JSON.stringify(this.param))
                })
            },

            setParam(code, data){
                this.EmptyParam()
                this.param.code = code
                for(let i in data){
                    this.param[i] = data[i]
                }
            },

            isJSON(str){
                if(typeof str == 'string'){
                    try{
                        let obj = JSON.parse(str)
                        if(typeof obj == 'object' && obj){
                            return true
                        }
                        else{
                            return false
                        }
                    }catch (e) {
                        console.log('error：'+str+'!!!'+e);
                        return false;
                    }
                }
                else{
                    return false
                }
            },

            ReadytoPlay(){
                this.setParam(1002, {"rid" : this.roomid, "id": this.myinfo.username})
                this.$ws.dispatch("WEBSOCKET_SEND", JSON.stringify(this.param))
            },

            chatSendMessage(param) {
                this.setParam(1005, param)
                // this.webSocketSend(JSON.stringify(this.param))
                this.$ws.dispatch("WEBSOCKET_SEND", JSON.stringify(this.param))
            },

            BacktoHome(){
                this.setParam(1050, {"rid": this.roomid, "id": this.myinfo.username})
                this.$ws.dispatch("WEBSOCKET_SEND", JSON.stringify(this.param))
            },

        },

        computed: {
            alertCon(){
                return this.$ws.getters.onEvent('string')
            }
        },

        watch:{
            alertCon : function (a, b) {
                if(a != b && a){
                    console.log(a)
                    if(this.isJSON(a)){
                        let data = JSON.parse(a)
                        if(data.code == 1001){
                            let enemy = ''
                            if(data.players[0] == this.myinfo.username){
                                enemy = data.players[1]
                            }
                            else{
                                enemy = data.players[0]
                            }
                            if(!enemy || enemy == null){
                                return
                            }
                            let url = "/user/?username=" + enemy
                            this.getRequest(url).then(resp => {
                                if(resp) {
                                    this.yourinfo = resp
                                }
                            })
                        }
                        else if(data.code == 1002){
                            if(data.id == this.yourinfo.username){
                                this.yourready = '已准备'
                            }
                            else if(data.id == this.myinfo.username){
                                this.myready = '已准备'
                                this.btnable = true
                            }
                        }
                        else if(data.code == 1003){
                            if(data.players[0] == this.myinfo.username){
                                this.me = true;
                                this.color = 1;
                            }
                            else{
                                this.me = false;
                                this.color = 2;
                            }
                            this.myready = '正在对局'
                            this.yourready = '正在对局'
                            this.initChessBoard()
                        }
                        else if(data.code == 1004){
                            this.onStep(data.coordinate, data.color, this.gridsize / 2 - 3)
                            this.circleChess(data.coordinate)
                            this.changeBeforeStep(data.coordinate)
                            if(data.player != this.myinfo.username){
                                if(this.checkOver(data.coordinate, data.color)){
                                    Message.error("你输了，呜呜呜┭┮﹏┭┮")
                                    this.setParam(1006, {"rid":this.roomid,"id":this.myinfo.username})
                                    this.$ws.dispatch("WEBSOCKET_SEND", JSON.stringify(this.param))
                                    this.myready = '未准备'
                                    this.yourready = '未准备'
                                    this.btnable = false
                                    return
                                }
                                this.me = true
                            }
                            else if(data.player == this.myinfo.username){
                                if(this.checkOver(data.coordinate, data.color)){
                                    Message.success("你获胜啦~~~~~~~~")
                                    this.setParam(1006, {"rid":this.roomid,"id":this.myinfo.username})
                                    this.$ws.dispatch("WEBSOCKET_SEND", JSON.stringify(this.param))
                                    this.myready = '未准备'
                                    this.yourready = '未准备'
                                    this.btnable = false
                                    return
                                }
                                this.me = false
                            }
                        }
                        else if(data.code == 1005){
                            this.chatrecord.push(data)
                        }
                        else if(data.code == 1050){
                            if(data.id == this.myinfo.username){
                                Message.info("你已经退出房间")
                            }
                            else if(data.id == this.yourinfo.username){
                                Message.info(data.id + "已经退出房间，你也退出房间了")
                            }
                            this.$router.push("/blog")
                        }
                    }
                }
            }
        }
    }
</script>

<style scoped>
    .round_icon{
        width: 100px;
        height: 100px;
        display: flex;
        border-radius: 50%;
        align-items: center;
        justify-content: center;
        overflow: hidden;
    }
    .name-label{
        width : 100px;
        text-align: center;
        margin-bottom: 20px;
    }
    .user-info-label{
        margin-left: 300px;
        margin-right: 300px;
        margin-top: 50px;
    }
</style>
