<template>
        <div class="talk_con2">
            <!-- 显示区域 -->
            <div class="talk_show" id="words">
                <!-- 根据vue对象中的数组，遍历出对应的标签。 -->
                <div v-for="i in chatrecord" :class="i.person== myinfo.username?'atalk':'btalk'">
                    <span>{{ i.person }}说：{{ i.words }}</span>
                </div>
            </div>
            <!-- 发送内容区域 -->
            <div class="talk_input">
                <!-- 请输入内容 -->
                <input type="text" class="talk_word" v-model='words' @keyup.enter="sendMsg">
                <!-- 按钮 -->
                <el-button style="float: right" @click="sendMsg">发送</el-button>
            </div>
        </div>
</template>

<script>
    export default {
        name: "ChatRoom",
        props:{
            'roomid':String,
            'myinfo':Object,
            'yourinfo':Object,
            'chatrecord':Array,
        },
        data(){
            return{
                words:'',
            }
        },
        methods:{
            sendMsg(){
                if(this.words != '' && this.yourinfo.username != '') {
                    let param = {
                        'rid':this.roomid,
                        'person': this.myinfo.username,
                        'words': this.words,
                    }
                    this.$emit("func",param);
                }
                this.words = ''
            },
            scrollButton(){
                this.$nextTick(function () {
                    let div = document.getElementById("words")
                    div.scrollTop = div.scrollHeight
                })
            }
        },
        watch:{
            chatrecord: {
                deep: true,
                handler: function (newVal, oldVal) {
                    console.log('newValue', newVal);
                    console.log('oldValue', oldVal);
                    this.scrollButton()
                }
            }
        }
    }
</script>

<style scoped>
    .talk_con2{
        width:600px;
        height:500px;
        border:1px solid #666;
        margin: 30px;
        float: right;
        background:#7adbd3;
    }
    .talk_show{
        width:580px;
        height:420px;
        border:1px solid #666;
        background:#fff;
        margin:10px auto 0;
        overflow:auto;
    }
    .talk_input{
        width:580px;
        margin:10px auto 0;
    }
    .whotalk{
        width:80px;
        height:30px;
        float:left;
        outline:none;
    }
    .talk_word{
        width:420px;
        height:26px;
        padding:0px;
        float:left;
        margin-left:10px;
        outline:none;
        text-indent:10px;
    }

    .atalk{
        margin:10px;
    }
    .atalk span{
        display:inline-block;
        background:#0181cc;
        border-radius:10px;
        color:#fff;
        padding:5px 10px;
    }
    .btalk{
        margin:10px;
        text-align:right;
    }
    .btalk span{
        display:inline-block;
        background:#ef8201;
        border-radius:10px;
        color:#fff;
        padding:5px 10px;
    }
</style>
